const state = {
  opened: true,
  device: 'desktop'
};

const mutations = {
  TOGGLE_SIDEBAR: state => { //   
    state.opened = !state.opened;//  Vuex  store  mutation
  },
  TOGGLE_DEVICE: (state, device) => {
    state.device = device;
  },
  CLOSE_SIDEBAR: (state) => {
    state.opened = false;
  },
  OPEN_SIDEBAR: (state) => {
    state.opened = true;
  }
};

const actions = {// Action  mutation
  toggleSideBar({ commit }) { // 
    commit('TOGGLE_SIDEBAR');
  },
  toggleDevice({ commit }, device) {
    commit('TOGGLE_DEVICE', device);
  },
  closeSideBar({ commit }) {
    commit('CLOSE_SIDEBAR');
  },
  openSideBar({ commit }) {
    commit('OPEN_SIDEBAR');
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};

