<template>
  <header class="header">
    <div>
      <i class="myicon" :class="iconclass" @click="test" />
      <el-breadcrumb separator="/" class="Breadcrumb_main">
        <transition-group name="breadcrumb">
          <el-breadcrumb-item v-for="(item,index) in levelList" :key="item.path">
            <span v-if="index===levelList.length-1" class="no-redirect">{{ item.meta.title }}</span>
            <a
              v-else-if="item.path === '/home'"
              @click.prevent="handleLink(item)"
            >{{ item.meta.title }}</a>
            <span v-else>{{ item.meta.title }}</span>
          </el-breadcrumb-item>
        </transition-group>
      </el-breadcrumb>
    </div>
    <div class="avatar">
      <div class="avatar_text">
        <el-dropdown class="avatar_but" size="small" trigger="click" @command="logout">
          <div class="avatar_img">
            <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png" />
            <i class="el-icon-caret-bottom" />
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="logout">
              <i class="el-icon-circle-close" />
              
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </header>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  components: {
  },
  props: {},
  data() {
    return {
      name: 'Admin',
      visible: false,
      levelList: null
    };
  },
  computed: {
    ...mapGetters(['opened']),
    iconclass() {
      return {
        'el-icon-s-fold': this.opened,
        'el-icon-s-unfold': !this.opened
      };
    },
    route() {
      return '';
    }
  },
  watch: {
    $route(route) {
      // if you go to the redirect page, do not update the breadcrumbs
      /* if (route.path.startsWith('/redirect/')) {
        return;
      }*/
      this.getBreadcrumb();
    }
  },
  beforeCreate() {
    /*
     *
     *beforeCreatedatamethods
     */
  },
  beforeMount() {
    /*
     *
     *
     * beforeMount 
     */
  },
  created() {
    /*
     *
     *createddatamethods
     *methodsdatacreated
     */
    this.getBreadcrumb();
  },
  // 
  beforeUpdate() {
    /*
     * 
     *beforeUpdatedata
     */
  },
  updated() {
    /*
     *data
     */
  },
  mounted() {
    /*
     *
     *
     *mountedmounted
     *DOMmounted
     */
  },
  beforeDestroy() {
    /*
     *Vue
     *datamethods
     */
    // 
  },
  destroyed() {
    /*
     *
     */
  },
  methods: {
    test() {
      this.$store.dispatch('app/toggleSideBar');
    },
    getBreadcrumb() {
      let matched = this.$route.matched.filter(
        item => item.meta && item.meta.title
      );
      const first = matched[0];
      if (!this.isDashboard(first)) {
        matched = [{ path: '/home', meta: { title: '' }}].concat(matched);
      }
      this.levelList = matched.filter(
        item => item.meta && item.meta.title && item.meta.breadcrumb !== false
      );
    },
    isDashboard(route) {
      const name = route && route.name;
      if (!name) {
        return false;
      }
      return name.trim().toLocaleLowerCase() === ''.toLocaleLowerCase();
    },
    handleLink(item) {
      const { redirect, path } = item;
      if (redirect) {
        this.$router.push(redirect);
        return;
      }
      this.$router.push(path);
    },
    async logout(command) {
      if (command === 'logout') {
        await this.$store.dispatch('users/logout');
        this.$router.push(`/login?redirect=${this.$route.fullPath}`);
      }
    }
  }
};
</script>
